import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Selector from "./components/Selector";
import Widget from "./components/Widget";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/widget/:logos">
          <Widget/>
        </Route>
        <Route path="/">
          <Selector/>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
