import React, {Component} from "react";
import {withRouter} from "react-router-dom";

class WidgetLink extends Component {

  constructor(props) {
    super(props);
    this.state = {
      src: [],
      srcEmpty: function (){
        return this.src.length > 0
      }
    }
  }

  componentDidMount() {
  }

  handleClick = (e) => {
    navigator.clipboard.writeText(this.props.getLink()).then(() => alert("Copied the link to clipboard"))
  }

  render() {
    return (
      <div className="text-center mb-3 mt-1">
        <button onClick={this.handleClick} className="btn btn-info" disabled={this.state.srcEmpty()}>{this.state.srcEmpty() ? 'Chose some logo\'s, then click here.' : 'Copy Link'}</button>
      </div>
    )
  }
}

export default withRouter(WidgetLink);